<template>
  <div class="qbox">
    <div class="container">
      <div class="row my-4 clearfix">
        <div class="col-md-12">
          <p style="font-size:1rem">
            <span>Question {{ qlen + 1 }}/{{ qtotal }}:</span>
            <span style="float: right">Correct: {{correct}} |  Incorrect: {{ incorrect }} </span>
          </p>
        </div>
      </div>
      <div class="row pl-3 my-5">
        <div>
          <h4 v-html="q"></h4>
        </div>
      </div>
      <div class="row">
        <div class="col-md-6" v-for="(answer, index) in ans" :key="index">
          <button
          
            type="button"
            class="btn btn-lg btn-block my-2 option border btn-primary"
            :class="changecolor[index]"
            @click.prevent="checkanswer( index, answer);"
          >
            <span v-html="answer"></span>
          </button>
        </div>
      </div>
      <div class="row mt-3">
        <div class="col-md-3">
          <!-- <button
            type="button"
            class="btn btn-primary btn-lg btn-block"
            @click="
              prev();
              
            "
            v-if="qlen > 0"
          >
            Prev Question
          </button> -->
        </div>
        <div class="col-md-3">
          <!-- <button
            type="button"
            class="btn btn-secondary btn-lg btn-block"
            @click="
              next();
              reset();
            "
            v-if="qlen < 9"
          >
            Next Question
          </button> -->
          <button v-if="test==qtotal" class="btn btn-primary btn-lg btn-block" @click="set() ">Show Results</button>
        </div>
        <div class="col-md-6 d-none my-2" id="corans">
          <button type="button" disabled class="btn btn-success btn-lg btn-block">
            <span>Correct Answer&nbsp;&nbsp;&nbsp;</span><br><b><span v-html="qdata.correct_answer"></span></b>
          </button>
        </div>
      </div>
      <div class="row d-none my-5" id="res">
        <div class="col-md-6"><h5> Correct : {{correct}}  |  Incorrect : {{incorrect}}  |  Skipped {{qtotal-(correct+incorrect)}}</h5>
         </div>
          
      </div>
    </div>
  </div>
</template>

<script>
import { computed } from "vue";
import _ from "lodash";
//const url = "https://opentdb.com/api.php?amount=10&category=18";
export default {
  name: "Qb",
  props: {
    qdata: Object,
    qlen: Number,
    prev: Function,
    next: Function,
    qtotal: Number,
  },
  data() {
    return {
      q: computed(() => {
        return this.qdata.question;
      }),
      ans: computed(() => {
        return _.shuffle([
          ...this.qdata.incorrect_answers,
          this.qdata.correct_answer,
        ]);
      }),
      changecolor: [],
      incorrect: 0,correct: 0,
      skip:0,
      test:0,
    };
  },
  methods: {
    
    reset() {
      this.changecolor = [];
      var x = document.getElementsByClassName("option");
      for (var i = 0; i < x.length; i++) {
        x[i].removeAttribute("disabled");
      }
       var m = document.getElementById('corans')
            m.classList.toggle('d-block');
            m.classList.add('d-none');
    },
    set(){
        {
            var m = document.getElementById('res')
            m.classList.toggle('d-none');
            }
    },
    checkanswer(index, answer) {
      this.reset();
     
      if (answer == this.qdata.correct_answer) {
        this.changecolor[index] = "btn-success";
        this.correct++
      } else {
        this.changecolor[index] = "btn-danger";
        this.incorrect++;
      }
     
      var x = document.getElementsByClassName("option");
      for (var i = 0; i < x.length; i++) {
        x[i].setAttribute("disabled", true);
      }
       var m = document.getElementById('corans')
            m.classList.remove('d-none');
            m.classList.add('d-block');
      this.test++;
      setTimeout(()=>{
        if(this.qlen<this.qtotal-1){
        this.next();
        this.reset();}
        },1500)
    },
  },
};
</script>

<style scoped></style>
