<template>
<div class="app">
  <Header/>
  <Qb v-if="quizdata.length" :qdata="quizdata[index]" :next="next" :prev="prev" :qlen="index" :qtotal="quizdata.length"/>
</div>
 
  
</template>

<script>
import {} from "vue";
import Header from "./components/Header.vue";
import Qb from "./components/Qbox.vue";

export default {
  name: "App",
  components: {
   Header,
    Qb,
  },
  data() {
    return {
      quizdata: [],
      index:0,
    };
  },
methods:{
next(){
  this.index++
},
prev(){
  this.index--
}
},
  created() {
    fetch("https://opentdb.com/api.php?amount=20&category=18", {
      method: "get",
    })
      .then((res) => res.json())
      .then((res) => {
       
        this.quizdata = res.results;
        
      });
  },
};
</script>

